import ComponentError from '../helpers/component-error'

const defaults = {
  ref: '[data-text-bind]',
  filters: getDefaultFilters()
}

export default function Binder (params) {
  params = {
    ...defaults,
    ...params,
    name: 'Binder'
  }

  const elements = document.querySelectorAll(params.ref)

  const instance = {
    elements,
    update,
    updateAll
  }

  elements.forEach(mount)

  return instance

  /**
   * 🔒 Private Methods
   */
  function mount (element) {
    const { id, filter, field } = getRefs(element)

    if (!field) {
      throw new ComponentError({
        name: params.name,
        message:
        `Erro ao montar componente.\nNenhum campo do Orquestra encontrado para a referência ${id}`,
        type: 'field_not_found'
      })
    }

    setValue({ element, field, filter })

    // attach listener using jQuery to workaround bootstrap-datepicker
    // not triggering native `change` event.
    $(field).on('change', e => {
      setValue({ element, field, filter })
    })
  }

  function getRefs (element) {
    const id = element.dataset.textBind
    const filter = element.dataset.textFilter
    const field = document.querySelector(`[xname=inp${id}]`)

    return {
      id,
      filter,
      field
    }
  }

  function setValue ({ element, field, filter }) {
    const hasFilter = filter && params.filters[filter]
    const value = hasFilter
      ? params.filters[filter](field.value)
      : field.value

    element.innerHTML = value
  }

  /**
   * 🔑 Public Methods
   */
  function update (element) {
    const { filter, field } = getRefs(element)

    if (!field) {
      return
    }

    setValue({ element, field, filter })
  }

  function updateAll () {
    instance.elements
      .forEach(element => {
        const { filter, field } = getRefs(element)
        setValue({ element, field, filter })
      })
  }
}

/**
 * Helpers
 */
function getDefaultFilters () {
  return {
    cnpj (value) {
      return value.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d)/, '$1.$2.$3/$4-$5')
    },

    titleCase (value) {
      return value
        .toLowerCase()
        .split(' ')
        .map(word => {
          return word.charAt(0).toUpperCase() + word.slice(1)
        }).join(' ')
    },

    empty (value) {
      if (value === null || value === '') {
        return 'Não informado'
      }
      return value
    }
  }
}
