export default function CreateCard (props) {
  props = {
    removeable: false,
    editable: false,
    ...props
  }

  return `
<div class="stack">
  <div class="stack-actions">
    ${props.removeable ? removeBtn : ''}
    ${props.editable ? editBtn : ''}
  </div>
  <h1 class="stack-title">${props.acaoTitulo}</h1>
  <p>${props.acaoDescricao}</p>
  <div class="stack-footer">
    <div class="columns -auto -align-center">
      <div class="column -grow">
        ${getStatusTag(props.acaoStatus)}
      </div>
      <div class="column">
        <i class="stack-label-icon fas fa-user"></i>
        <span class="stack-label">${props.acaoResponsavel}</span>
      </div>
      <div class="column">
        <i class="stack-label-icon fas fa-bell"></i>
        <span class="stack-label">${props.acaoMonitoramento} dias</span>
      </div>
      <div class="column">
        <i class="stack-label-icon fas fa-calendar"></i>
        <span class="stack-label">${getActionDate(props.acaoPrazo, props.acaoTermino)}</span>
      </div>
    </div>
  </div>
</div>
`
}

const removeBtn = `
<button class="btn btn-link -red btn-small stack-action" data-vtable-remove>
  <i class="btn-icon fas fa-trash"></i>
  <span>Excluir</span>
</button>
`
const editBtn = `
<button class="btn btn-link btn-small stack-action" data-vtable-edit>
  <i class="btn-icon fas fa-edit"></i>
  <span>Editar</span>
</button>
`

export function getStatusTag (status = '') {
  const text = status
  status = status.toLowerCase()

  if (status === 'em andamento') {
    return `
    <div class="tag -blue">
      <i class="tag-icon fas fa-play-circle"></i>${text}
    </div>
    `
  }

  if (status === 'pausada') {
    return `
    <div class="tag -yellow">
      <i class="tag-icon fas fa-pause-circle"></i>${text}
    </div>
    `
  }

  if (status === 'não iniciada') {
    return `
    <div class="tag">
      <i class="tag-icon fas fa-minus-circle"></i>${text}
    </div>
    `
  }

  if (status === 'concluída') {
    return `
    <div class="tag -green">
      <i class="tag-icon fas fa-check-circle"></i>${text}
    </div>
    `
  }

  if (status === 'cancelada') {
    return `
    <div class="tag -red">
      <i class="tag-icon fas fa-times-circle"></i>${text}
    </div>
    `
  }

  return null
}

function getActionDate (dueDate, doneDate) {
  if (doneDate) {
    return doneDate
  }

  return dueDate
}
