import ActionsRisks from './modules/action-plan/actions-risks'
import ListRemote from './modules/action-list/list-remote'

const ORQUESTRA_ACTION_PLAN_CODFLOW = process.env.ORQUESTRA_ACTION_PLAN_CODFLOW
const ORQUESTRA_ACTION_CODFLOW = process.env.ORQUESTRA_ACTION_CODFLOW

const Report = function () {
  const $refs = {
    codflow: document.querySelector('#inpCodFlow'),
    actionsPlan: document.querySelector('#section-plan'),
    actionsList: document.querySelector('#section-actions'),
    actionsTable: document.querySelector('#actions-table'),
    action: document.querySelector('#section-action'),
    drawerBtns: document.querySelectorAll('[data-drawer-open]')
  }

  const state = {
    emitter: null
  }

  return {
    init
  }

  /**
   * 🔑 Public Methods
   */
  function init () {
    state.flow = getProcessByCodflow($refs.codflow.value)

    mount(state.flow)
  }

  /**
   * 🔒 Private Methods
   */
  function getProcessByCodflow (codflow) {
    if (ORQUESTRA_ACTION_PLAN_CODFLOW === codflow) {
      return 'plano'
    }

    if (ORQUESTRA_ACTION_CODFLOW === codflow) {
      return 'acao'
    }

    return null
  }

  function mount (flow) {
    ActionsRisks.mount()

    $refs.drawerBtns
      .forEach(btn => {
        btn.remove()
      })

    if (flow === 'plano') {
      ListRemote.mount()

      $refs.actionsList.classList.remove('u-hidden')
      $refs.action.classList.add('u-hidden')
    }

    if (flow === 'acao') {
      $refs.actionsPlan.classList.add('u-hidden')

      fillActionFormHelperFields()
    }
  }

  function fillActionFormHelperFields () {
    $refs.actionsTable
      .querySelectorAll('[xname]')
      .forEach(fillEquivalentField)
  }

  function fillEquivalentField (field) {
    const id = field.getAttribute('xname')
    const equivalent = document.querySelector(`[xname=${id}_form]`)

    if (!equivalent) {
      return
    }

    equivalent.value = field.value
  }
}

document
  .addEventListener('DOMContentLoaded', () => {
    Report().init()
  })
