import ActionsService from '../api/api.actions'
import CreateCard from './list-card'

const ListRemote = function () {
  const $refs = {
    section: document.querySelector('#section-actions'),
    vtable: document.querySelector('#actions-list'),
    codFlowExecute: document.querySelector('#inpCodFlowExecute')
  }

  const state = {
    codFlowExecute: $refs.codFlowExecute.value
  }

  return {
    mount
  }

  /**
   * 🔑 Public Methods
   */
  function mount () {
    getActions(state.codFlowExecute)
  }

  /**
   * 🔒 Private Methods
   */
  function getActions (codFlowExecute) {
    addLoadingtState()

    ActionsService.getActions({
      codFlowExecute
    })
      .then(render)
      .finally(removeLoadingState)
  }

  function render (actions) {
    const markup = Object.values(actions)
      .map(CreateCard)
      .join('')

    $refs.vtable
      .insertAdjacentHTML('beforeend', markup)
  }

  function addLoadingtState () {
    $refs.section.classList.add('-loading')
    $refs.vtable.classList.add('-loading')
  }

  function removeLoadingState () {
    $refs.section.classList.remove('-loading')
    $refs.vtable.classList.remove('-loading')
  }
}

export default ListRemote()
