const ORQUESTRA_BASE_URL = process.env.ORQUESTRA_BASE_URL
const ORQUESTRA_REST_PATH = process.env.ORQUESTRA_REST_PATH
const ORQUESTRA_SOAP_PATH = process.env.ORQUESTRA_SOAP_PATH
const DATASOURCE_BASE_URL = process.env.DATASOURCE_BASE_URL
const DATASOURCE_ACTIONS = process.env.DATASOURCE_ACTIONS
const ORQUESTRA_ACTION_PLAN_CODFLOW = process.env.ORQUESTRA_ACTION_PLAN_CODFLOW
const ORQUESTRA_ACTION_CODFLOW = process.env.ORQUESTRA_ACTION_CODFLOW
const ORQUESTRA_ACTION_ELEMENT_ALIAS = process.env.ORQUESTRA_ACTION_ELEMENT_ALIAS

const ORQUESTRA_SOAP_UPDATE_FORM =
  `${ORQUESTRA_BASE_URL}${ORQUESTRA_SOAP_PATH}/instance.asmx/UpdateForm01`

const ORQUESTRA_REST_INSTANCES =
  `${ORQUESTRA_BASE_URL}${ORQUESTRA_REST_PATH}/instances`

const ORQUESTRA_DATASOURCE_ACTIONS =
  `${DATASOURCE_BASE_URL}${DATASOURCE_ACTIONS}`

/**
 * 🔑 Public Methods
 */
const getActions = function ({ codFlowExecute }) {
  const url = new URL(ORQUESTRA_DATASOURCE_ACTIONS)

  const params = {
    inpCodFlowExecuteStarter: codFlowExecute
  }

  url.search = new URLSearchParams(params).toString()

  return fetch(url)
    .then(res => res.json())
    .then(({ success }) =>
      success.reduce(parseData, {})
    )
}

const createAction = function (props) {
  const {
    token,
    codFlowExecute,
    formValues
  } = props

  const payload = {
    flow: { id: parseInt(ORQUESTRA_ACTION_CODFLOW) },
    values: formValuesToRestFormat(formValues),
    CodFlowExecuteParent: parseInt(codFlowExecute),
    CodFlowParent: parseInt(ORQUESTRA_ACTION_PLAN_CODFLOW),
    DsFlowElementAlias: ORQUESTRA_ACTION_ELEMENT_ALIAS,
    simulation: false
  }

  const url = ORQUESTRA_REST_INSTANCES

  return fetch(url, {
    method: 'POST',
    body: JSON.stringify(payload),
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${token}`
    }
  })
    .then(res => res.json())
}

const updateActionsList = function (props) {
  const {
    codFlowExecute,
    formValues,
    token
  } = props

  const payload = {
    token,
    codFlowExecute,
    XmlValues: formValuesToSoapFormat(formValues)
  }

  const url = ORQUESTRA_SOAP_UPDATE_FORM

  return fetch(url, {
    method: 'POST',
    body: formatSoapPayload(payload),
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
    }
  })
    .then(res => res.text())
    .then(xmlString => $.parseXML(xmlString))
}

export default {
  getActions,
  createAction,
  updateActionsList
}

/**
 * 🔒 Private Methods
 */
const parseData = (actions, row) => {
  const { fields, txt } = row

  if (!actions[txt]) {
    actions[txt] = {}
  }

  actions[txt][fields.id] = fields.value

  return actions
}

const formValuesToRestFormat = formValues =>
  Object.entries(formValues)
    .map(([name, value]) => ({ name, value }))

const formatObjectToXmlString = formValues =>
  Object.entries(formValues)
    .map(([id, value]) => {
      if (Array.isArray(value)) {
        return value
          .map(formatObjectToXmlString)
          .join('')
      } else {
        return `<${id}><![CDATA[${value}]]></${id}>`
      }
    })
    .join('')

const formValuesToSoapFormat = formValues => {
  const fields = formatObjectToXmlString(formValues)

  return `<form>${fields}</form>`
}

const formatSoapPayload = payload =>
  Object.entries(payload)
    .map(([prop, value]) => `${prop}=${value}`)
    .join('&')
