import Binder from '../../components/binder'

const ActionsRisks = function () {
  const $refs = {
    section: document.querySelector('#section-risks')
  }

  return {
    mount
  }

  /**
   * 🔑 Public Methods
   */
  function mount () {
    Binder({
      filters: {
        arrow: getArrowByLevel,
        treatmentIcon: getTreatmentIcon
      }
    })

    showRisksHeader()
  }

  /**
   * 🔒 Private Methods
   */
  function showRisksHeader () {
    $refs.section
      .classList.remove('u-hidden')
  }
}

export default ActionsRisks()

/**
 * 💡 Helpers
 */
function getArrowByLevel (level) {
  let icon = null
  const text = level
  level = level.toLowerCase()

  if (level === 'baixo' || level === 'baixa') {
    icon = '<i class="fas fa-long-arrow-alt-down u-color-blue-300"></i>'
  }

  if (level === 'médio' || level === 'média') {
    icon = '<i class="fas fa-minus u-color-grey-500"></i>'
  }

  if (level === 'alto' || level === 'alta') {
    icon = '<i class="fas fa-long-arrow-alt-up u-color-blue-700"></i>'
  }

  return `${icon} ${text}`
}

function getTreatmentIcon (treatment) {
  if (treatment === 'Explorar') {
    return '<i class="fas fa-mountain"></i>'
  }

  if (treatment === 'Mitigar') {
    return '<i class="fas fa-umbrella"></i>'
  }

  if (treatment === 'Monitorar') {
    return '<i class="fas fa-heartbeat"></i>'
  }

  if (treatment === 'Transferir') {
    return '<i class="fas fa-share"></i>'
  }

  if (treatment === 'Aceitar') {
    return '<i class="fas fa-check-double"></i>'
  }

  if (treatment === 'Evitar') {
    return '<i class="fas fa-exclamation-triangle"></i>'
  }

  return null
}
